@use "../../../utils/themeConstants";

.ticket-item {
  display: flex;
  flex-direction: row;

  background-color: themeConstants.$item-background;
  box-shadow: 10px 10px 20px 0px #0D282917, -10px -10px 20px 0px #0D282917;
  border-radius: 10px;
}

.ticket-item__left-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  padding: 20px;
  border-right: 1px solid rgba(themeConstants.$grey-light, 0.3);
}

.ticket-item__left-section__company-logo {
  height: 40px;
  width: auto;
}

.ticket-item__left-section__purchase-button {
  width: 180px;
  text-align: center;
  padding: 10px;
  background-color: themeConstants.$orange;
  border-radius: 10px;

  color: white;
  font-size: 20px;
}

.ticket-item__right-section {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 20px 30px;
}

.ticket-item__right-section__top {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
}

.ticket-item__right-section__top__time {
  font-size: 36px;
}

.ticket-item__right-section__top__plane-changes {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.ticket-item__right-section__top__plane-changes__count {
  color: themeConstants.$grey-light;
}

.ticket-item__right-section__top__plane-changes__icon {

}

.ticket-item__right-section__bottom {
  flex: 1;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
}

.ticket-item__right-section__bottom__block {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 4px;
}

.ticket-item__right-section__bottom__block__location {
  font-weight: bold;
}

.ticket-item__right-section__bottom__block__date {
  color: themeConstants.$grey-light;
}
