@use "./utils/themeConstants";

.main-container {
  gap: 10px;

  display: flex;
  flex-direction: column;
  align-items: center;

  margin: 10px;
}

.section {
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  width: calc(100% - 20px);
  max-width: 800px;
  gap: 10px;
}

.show-below-720 {
  display: none;
  @media(max-width: 720px) {
    display: initial;
  }
}

.show-above-720 {
  display: initial;

  @media(max-width: 720px) {
    display: none;
  }
}