@use "../../utils/themeConstants";

.plane-ticket-aggregator__logo {
  align-self: center;
}

.plane-ticket-aggregator__content {
  display: flex;
  flex-direction: row;
  width: 100%;
  align-items: flex-start;
  gap: 20px;
}

.plane-ticket-aggregator__content__filters {
  display: flex;
  flex-direction: column;
  align-self: flex-start;
  padding: 20px;
  gap: 30px;

  background-color: themeConstants.$item-background;
  border-radius: 10px;
  box-shadow: 10px 10px 20px 0px #0D282917, -10px -10px 20px 0px #0D282917;
}

.plane-ticket-aggregator__content__filters__item {
  display: flex;
  flex-direction: column;
  gap: 10px;

  font-weight: bold;
}

.plane-ticket-aggregator__content__filters__item__currencies {
  display: flex;
  flex-direction: row;
}

.plane-ticket-aggregator__content__filters__item__currencies__item {
  flex: 1;
  padding: 10px 20px;
  background-color: white;
  border: 1px solid themeConstants.$grey-light;

  &--selected {
    color: white;
    background-color: themeConstants.$blue;
    border: 1px solid themeConstants.$grey-light;

    &:disabled {
      color: white !important;
    }
  }

  &:disabled {
    color: rgba(themeConstants.$grey-light, 0.5);
    border: 1px solid themeConstants.$grey-light !important;
  }

  &:hover {
    border: 1px solid themeConstants.$blue;
  }

  &:first-child {
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
  }
  &:last-child {
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
  }
}



.plane-ticket-aggregator__content__filters__item__stops-list {
  display: flex;
  flex-direction: column;
  margin: 0px -20px 0px -20px;
}

.plane-ticket-aggregator__content__filters__item__stops-list__item {
  display: flex;
  flex-direction: row;
  padding: 4px 20px;
  gap: 10px;
  align-items: center;
  justify-content: space-between;
  background: none;

  &:hover {
    background-color: themeConstants.$blue-light;

    .plane-ticket-aggregator__content__filters__item__stops-list__item__only-button {
      display: block;
    }
  }
}

.plane-ticket-aggregator__content__filters__item__stops-list__item__count-button {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  background: none;
  gap: 6px;
}

.plane-ticket-aggregator__content__filters__item__stops-list__item__count-button__icon {
  width: 18px;
  height: 18px;
  border: 1px solid themeConstants.$grey;
  border-radius: 4px;
  font-weight: bold;

  &--selected {
    color: themeConstants.$blue;
    border: 1px solid themeConstants.$blue;
  }
}

.plane-ticket-aggregator__content__filters__item__stops-list__item__only-button {
  display: none;
  color: themeConstants.$blue;
  font-weight: bold;
  background: none;

  &:hover {
    display: block;
  }
}

.plane-ticket-aggregator__content__results {
  display: flex;
  flex-direction: column;
  gap: 10px;
  flex: 1;
}
